import React from 'react';
import { CardData } from '../types';
import Card from './Card';

export default function GameBoard({ allCards }: { allCards: CardData[] }) {
  
  // ====================================================================
  // üéõÔ∏è CONFIGURATION G√âOM√âTRIQUE
  // ====================================================================

  // 1. POSITION DES JOUEURS (Mains)
  const DIST_NORD = 240;
  const DIST_SUD  = 240;
  const DIST_OUEST= 400;
  const DIST_EST  = 400;

  // 2. POSITION DES TEXTES (Directions)
  // On les met plus proches du centre que les joueurs (environ √† mi-chemin)
  const LABEL_DIST_Y = 140; // Distance pour Nord/Sud depuis le centre
  const LABEL_DIST_X = 300; // Distance pour Ouest/Est depuis le centre

  // 3. CONFIGURATION DE L'√âVENTAIL
  const HAND_RADIUS = 600; 
  const ANGLE_STEP = 4;

  // Debug
  const SHOW_DEBUG_CENTER = false; 

  // ====================================================================

  const getCardStyle = (card: CardData) => {
      // --- LOGIQUE GAGN√â ---
      if (card.status === 'WON') {
          const winPos = [
              `translate(0px, 500px)`,    // Sud
              `translate(-600px, 0px)`,   // Ouest
              `translate(0px, -500px)`,   // Nord
              `translate(600px, 0px)`     // Est
          ];
          return {
              transform: `translate(-50%, -50%) ${winPos[card.winnerIndex || 0]} scale(0.4)`,
              opacity: 0,
              zIndex: card.zIndex
          };
      }

      // --- LOGIQUE TABLE ---
      if (card.status === 'TABLE') {
          const rX = (card.tableRotation * 5) % 15 - 7;
          const rY = (card.tableRotation * 7) % 15 - 7;
          return {
              transform: `translate(-50%, -50%) translate(${rX}px, ${rY}px) rotate(${card.tableRotation}deg) scale(1.1)`,
              opacity: 1,
              zIndex: card.zIndex
          };
      }

      // --- LOGIQUE MAIN (√âVENTAIL) ---
      const cardsInHand = allCards.filter(c => c.owner === card.owner && c.status === 'HAND');
      
      const colorPriority: Record<string, number> = { 'Blue': 1, 'Green': 2, 'Pink': 3, 'Yellow': 4, 'Rocket': 5 };
      cardsInHand.sort((a, b) => {
          if (a.color !== b.color) return colorPriority[a.color] - colorPriority[b.color];
          return a.value - b.value;
      });
      
      const myIndex = cardsInHand.findIndex(c => c.id === card.id);
      const total = cardsInHand.length;

      const angle = (myIndex - (total - 1) / 2) * ANGLE_STEP;
      const centerHTML = "translate(-50%, -50%)";

      let playerRotation = 0;
      let distance = 0;

      switch(card.owner) {
        case 0: // SUD
            playerRotation = 0; 
            distance = DIST_SUD; 
            break;
        case 1: // OUEST
            playerRotation = 90; 
            distance = DIST_OUEST; 
            break;
        case 2: // NORD
            playerRotation = 180; 
            distance = DIST_NORD; 
            break;
        case 3: // EST
            playerRotation = -90; 
            distance = DIST_EST; 
            break;
      }
      
      const transform = `
        ${centerHTML} 
        rotate(${playerRotation}deg) 
        translate(0px, ${distance + HAND_RADIUS}px) 
        rotate(${angle}deg) 
        translate(0px, -${HAND_RADIUS}px)
      `;

      return { transform, opacity: 1, zIndex: myIndex };
  };

  // Style de base pour les textes
  const labelStyle: React.CSSProperties = {
      position: 'absolute',
      color: 'rgba(255,255,255,0.15)', // Un peu plus discret
      fontWeight: 'bold',
      letterSpacing: '0.2em',
      fontSize: '1.2rem',
      pointerEvents: 'none',
      textAlign: 'center',
      width: '200px',
      // On centre le point de pivot du texte pour que la rotation soit propre
      transformOrigin: 'center center' 
  };

  return (
    <div className="w-full h-full relative bg-[#2c3e50] overflow-hidden shadow-inner flex items-center justify-center">
        
        {/* LE POINT Z√âRO ABSOLU */}
        <div className="relative w-0 h-0 flex items-center justify-center">

            {/* --- TEXTES DIRECTIONS --- */}
            {/* Note : J'utilise LABEL_DIST_Y et LABEL_DIST_X ici */}

            {/* NORD : On monte de LABEL_DIST_Y */}
            <div style={{ ...labelStyle, transform: `translate(0px, -${LABEL_DIST_Y}px)` }}>
                NORD
            </div>

            {/* SUD : On descend de LABEL_DIST_Y */}
            <div style={{ ...labelStyle, transform: `translate(0px, ${LABEL_DIST_Y}px)` }}>
                SUD
            </div>

            {/* OUEST : On va √† gauche de LABEL_DIST_X et on pivote */}
            <div style={{ ...labelStyle, transform: `translate(-${LABEL_DIST_X}px, 0px) rotate(90deg)` }}>
                OUEST
            </div>

            {/* EST : On va √† droite de LABEL_DIST_X et on pivote */}
            <div style={{ ...labelStyle, transform: `translate(${LABEL_DIST_X}px, 0px) rotate(-90deg)` }}>
                EST
            </div>

            {/* --- CARTES --- */}
            {allCards.map((card) => {
                const style = getCardStyle(card);
                const speed = card.status === 'WON' ? '1000ms' : '600ms';
                
                return (
                    <div 
                        key={card.id}
                        className="absolute top-0 left-0 w-16 aspect-[2/3] transition-all ease-in-out"
                        style={{ 
                            ...style, 
                            transitionProperty: 'transform, opacity', 
                            transitionDuration: speed,
                            willChange: 'transform'
                        }}
                    >
                        <Card color={card.color} value={card.value} className="w-full h-full shadow-2xl border border-black/30 rounded-lg" />
                    </div>
                );
            })}

        </div>
      </div>
  );
}